import {Link} from "react-router-dom"

const Content = ({ backEndData, name, setName, message, handleEdit, handleDelete }) => {

  

  return (
    <>
      <section className="">

        <div className="container mt-5">


          <div className="col-auto my-4">
            <Link to={`/add`}>
                <button className="btn btn-sm btn-primary">Add User</button>
            </Link>
          </div>

          <table className="table table-dark table-striped text-center">
            <thead>
              <tr>
                <th>name</th>
                <th>email</th>
                <th>phone</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {backEndData.map((user, index) => (
                <tr key={index}>
                  <td>{ user.name}</td>  
                  <td>{ user.email}</td>  
                  <td>{ user.phone}</td>  
                  <td>
                      <Link to={`/edit/${user._id}`}>
                          <button className="btn btn-primary me-1">Edit</button>
                      </Link>
                      
                      <button className="btn btn-danger" onClick={() => handleDelete(user._id)}>Delete</button>
                  </td>  
                </tr>
                ))}
            </tbody>
          </table>                
        </div>
      </section>
    </>
  );
};

export default Content;
