import { useParams, Link } from "react-router-dom";
import { useEffect, useState } from "react";

const Edit = ({ handleEdit, backEndData,
    editName, setEditName,
    editEmail, editPhone, setEditEmail, setEditPhone, editBackEndData }) => {


    const { id } = useParams();

    const editItem = backEndData.find(item => item._id === id)

    useEffect(() => {
        if (editItem) {
            setEditPhone(editItem.phone)
            setEditEmail(editItem.email)
            setEditName(editItem.name)
        }    
    }, [editItem, setEditPhone, setEditEmail, setEditName])

    return (
        <>
            <section>
                <div className="container">
                             <form className="" onSubmit={(e) => e.preventDefault()}>
                                    <div className="form-group">
                                        <label className='form-label'>Enter Name</label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            name="name"
                                            placeholder="Enter your name"
                                            id=""
                                            value={editName}
                                            onChange={(e) => setEditName(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className='form-label'>Enter Email</label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            name="email"
                                            placeholder="Enter your email"
                                            id=""
                                            value={editEmail}
                                            onChange={(e) => setEditEmail(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className='form-label'>Enter phone</label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            name="phone"
                                            placeholder="Enter your phone number"
                                            id=""
                                            value={editPhone}
                                            onChange={(e) => setEditPhone(e.target.value)}
                                        />
                                    </div>
                                    <div className="col-auto">
                                        <button className="btn btn-primary" onClick={(e) => handleEdit(id)} type="submit">Submit</button>
                                    </div>
                                </form>
                    </div>               
            </section>
        </>
    )
}

export default Edit
