import { useState, useEffect, useMemo } from "react";
import {Switch, Route, useHistory} from "react-router-dom"
import axios from "axios"
import Content from "./components/Content"
import Add from "./components/Add"
import Edit from "./components/Edit"
import Header from "./components/Header"
import Footer from "./components/Footer"
import Missing from "./components/Missing"
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()

function App() {

  const [backEndData, setBackEndData] = useState([{}]);
  const [message, setMessage] = useState('')

  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [phone, setPhone] = useState("")
  const [editBody, setEditBody] = useState({});
  const [editName, setEditName] = useState("")
  const [editEmail, setEditEmail] = useState("")
  const [editPhone, setEditPhone] = useState("")
  const [editBackEndData, setEditBackEndData] = useState([{}]);


  const history = useHistory();


  useEffect(() => {
    const fetchData = async () => {
      const { data } = await axios.get("/api/route");
      setBackEndData(data.users);
      console.log(data.users);
    }

    fetchData();
  }, [])


  const handleDelete = async (id) => {
    try {
      const {data} = await axios.delete(`/api/route/delete/${id}`);
      history.push("/")
    } catch (err) {
      console.log(`Error: ${err.message}`)
    }
  }


  const handleEdit = async (id) => {
    const updatedUSer = { _id: id, name: editName, phone: editPhone,  email: editEmail}
    try {
      await axios.post(`/api/route/update/${id}`, updatedUSer)
      setEditEmail("");
      setEditName("");
      setEditPhone("");
      history.push("/");
    } catch (err) {
      console.log(`Error: ${err.message}`)
    }
  }


  const handleSubmit = async (e) => {
    e.preventDefault();
    const newUser = { name: name, email: email, phone: phone }
    try {
      await axios.post(`/api/route/save`, newUser);
      setName('');
      setPhone('');
      setEmail('');
      history.push("/");
      toast.success("Sent");
    } catch (err) {
      console.log(`Error: ${err.message}`);
    }
  }


  return (
    <div className="App">
      <Header />

        <Switch>

          <Route path="/" exact>
            <Content
              backEndData={backEndData}
              name={name}
              setName={setName}
              message={message}
              handleDelete={handleDelete}
              handleEdit={handleEdit}
            />  
          </Route>

            <Route path="/add" exact>
                <Add
                name={name}
                setName={setName}
                email={email}
                setEmail={setEmail}
                phone={phone}
                setPhone={setPhone}
                handleSubmit={handleSubmit}
              />
            </Route>

            <Route path="/edit/:id" exact>
                <Edit
                  editBackEndData={editBackEndData}
                  setEditBackEndData={setEditBackEndData}
                  editName={editName}
                  setEditName={setEditName}
                  setEditPhone={setEditPhone}
                  setEditEmail={setEditEmail}
                  editEmail={editEmail}
                  backEndData={backEndData}
                  setBackEndData={setBackEndData}
                  editPhone={editPhone}
                  handleEdit={handleEdit}
                />
            </Route>
          
        </Switch>
      <Footer/>     
    </div>
  );
}

export default App;
